/**
 * Contest (Module)
 * Mdulo encargado de manejar las maratones de programacin
 * @export
 * @class Contest
 */
export class Syllabus {
  /**
   * Se encarga del enrutamiento dentro de la aplicacin
   * @param {any} config - Configuracin de la aplicacin
   * @param {any} router - Enrutador principal de la aplicacin
   */
  configureRouter (config, router) {
    config.title = 'UFPS Training Center'
    config.map([
      {
        route: '',
        name: 'contest',
        moduleId: 'modules/contest/home-contest/home-contest',
        settings: {
          roles: ['coach', 'student', 'admin']
        }
      },
      {
        route: 'nueva',
        name: 'create',
        moduleId: 'modules/contest/create-contest/create-contest',
        settings: {
          roles: ['coach', 'student', 'admin']
        }
      }
    ])
    this.router = router
  }
}
